import "bootstrap/dist/css/bootstrap.min.css";
import { useState } from "react";
import { Link } from "react-router-dom";
import { Navbar, Container, Button } from "react-bootstrap";
import DatalistInput from "react-datalist-input";
import { ModalUser } from "../ModalUser";
import "react-datalist-input/dist/styles.css";
import "./index.css";

export function HeaderUp() {
  const [input, setInput] = useState("");
  const [address, setAddress] = useState([]);

  const handleChange = (e) => {
    setInput(e.target.value);
  };

  const handleSubmit = (e) => {
    setAddress((prev) => {
      return [...prev, { id: address.length, value: input }];
    });
  };

  const selectChange = (item) => {
    setInput(item.value);
  };

  return (
    <Navbar bg="light" expand="lg">
      <Link id="logo" to="/">
        <img
          alt="logo"
          src="https://grandriverenergy.com/wp-content/uploads/2018/07/eda-logo-png-transparent-e1530807572644.png"
          width="auto"
          height="100"
        />{" "}
      </Link>
      <Container fluid className="headerUp">
        <div className="form-control">
          <DatalistInput
            placeholder=" "
            onSelect={(item) => selectChange(item)}
            onChange={handleChange}
            items={address}
          />
        </div>
        <div className="textRight">
          {address.map((el, key) => {
            if (key + 1 === address.length) {
              return <div key={el.id}>{el.value}</div>;
            }
          })}
        </div>
        <Button variant="outline-success" onClick={handleSubmit}>
          
        </Button>
        <ModalUser />
        <Link to="sale">
          <img
            src="https://img.icons8.com/pastel-glyph/344/shopping-trolley--v1.png"
            height="50"
            alt="img "
          />
        </Link>
      </Container>
    </Navbar>
  );
}
